import React from 'react';
import HowTo from '../how-to'
import Standalone from '../layouts/standalone'
import { graphql } from 'gatsby'
import SEO from '../seo'
import ListingSummary from '../listing-summary'
import Share from '../share'
import Disqus from 'disqus-react'

const HowToPage = ({ data }) => {
  const { title, description, tags, source } = data.markdownRemark.frontmatter;
  const { commonKeywords } = data.site.siteMetadata;
  const allCategories = data.categories.edges.map(edge => edge.node);

  return (
    <Standalone>
      <SEO title={ title } description={ description } keywords={[...tags, ...commonKeywords]}/>
      <main>
        <div className="container-fluid">
          <div className="row">
            <div className="col-md-8">
              <h1 className="title--header">{ title }</h1>
              <HowTo source={ source } title={ title } />
              <Share/>
              <Disqus.DiscussionEmbed shortname="expatty" />
            </div>
            <div className="col-md-4">
              {
                data.related.edges.map(({ node }, i) => {
                  const listingIcons =
                    node.frontmatter.tags
                      .map(tg => allCategories.find(catNode => catNode.frontmatter.tag === tg))
                      .filter(catNode => catNode)
                      .map(catNode => ({
                        icon: catNode.frontmatter.icon,
                        name: catNode.frontmatter.name,
                        path: catNode.fields.slug
                      }));
                  return (
                    <ListingSummary
                      key={ i }
                      description={ node.frontmatter.description }
                      title={ node.frontmatter.title }
                      url={ node.fields.slug }
                      icons={ listingIcons }/>
                  )
                })
              }
            </div>
          </div>
          {/*<div className="row">
            <div className="col-md">
              <Share/>
              <Disqus.DiscussionEmbed shortname="expatty" />
            </div>
          </div>*/}
        </div>
      </main>
    </Standalone>
  );
};

export default HowToPage;

export const pageQuery = graphql`
  query HowToPages($slug: String!, $related: [String!]!) {
      site {
          siteMetadata {
              commonKeywords
          }
      }
      markdownRemark(fields: { slug: { eq: $slug } }) {
          frontmatter {
              title
              description
              tags
              source
          }
      }
      related: allMarkdownRemark(filter: {frontmatter: {type: {eq: "listing"}, tags: {in: $related}}}) {
          edges {
              node {
                  frontmatter {
                      title
                      description
                      icon
                      tags
                  }
                  fields {
                      slug
                  }
              }
          }
      }
      categories: allMarkdownRemark(filter: {frontmatter: {type: {eq: "category"}}}) {
          edges {
              node {
                  frontmatter {
                      tag
                      name
                      icon
                  }
                  fields {
                      slug
                  }
              }
          }
      }
  }
`;