import React, { useEffect, useRef } from 'react'

const HowTo = ({ source, title }) => {

  const howToIframe = useRef(null);

  const resizeIframe = () => {
    howToIframe.current.style.height = howToIframe.current.contentWindow.document.body.scrollHeight + 'px';
  };

  useEffect(() => {
    window.addEventListener('resize', resizeIframe);
    return () => window.removeEventListener('resize', resizeIframe);
  });

  return (
    <iframe ref={ howToIframe } className="how-to-iframe" src={ source } scrolling="no" frameBorder={ 0 } title={ title } onLoad={ resizeIframe } />
  );
};

export default HowTo;